import React from 'react';

export interface IFooterSocialMediaLinksProps {
  /**
   * Adds classname to the social media links
   * */
  className?: string;
  [others: string]: any;
}

const FooterSocialMediaLinks: React.FC<IFooterSocialMediaLinksProps> = (props) => {
  const {className, children, ...rest} = props;
  return (
    <div className={`footer-social-media ${className || ''}`} {...rest}>
      {children}
    </div>
  );
};

export default FooterSocialMediaLinks;
