import React from 'react';

export interface IFooterPoliciesLinksProps {
  /**
   * Adds classname to the policies links
   * */
  className?: string;
  [others: string]: any;
}

const FooterPoliciesLinks: React.FC<IFooterPoliciesLinksProps> = (props) => {
  const {className, children, ...rest} = props;
  return (
    <div className={`footer-policies-links ${className || ''}`} {...rest}>
      {children}
    </div>
  );
};

export default FooterPoliciesLinks;
