import React from 'react';

export interface IFooterUsefulLinksProps {
  /**
   * Adds classname to the useful links
   * */
  className?: string;
  [others: string]: any;
}

const FooterUsefulLinks: React.FC<IFooterUsefulLinksProps> = (props) => {
  const {className, children, ...rest} = props;
  return (
    <div className={`footer-useful-links ${className || ''}`} {...rest}>
      {children}
    </div>
  );
};

export default FooterUsefulLinks;
