import React, {useEffect, useState} from 'react';
import FooterBrand from './footerBrand';
import FooterLogo from './footerLogo';
import FooterPoliciesLinks from './footerPoliciesLinks';
import FooterSocialMediaLinks from './footerSocialMediaLinks';
import FooterUsefulLinks from './footerUsefulLinks';

export {default as FooterBrand} from './footerBrand';
export {default as FooterLogo} from './footerLogo';
export {default as FooterPoliciesLinks} from './footerPoliciesLinks';
export {default as FooterSocialMediaLinks} from './footerSocialMediaLinks';
export {default as FooterUsefulLinks} from './footerUsefulLinks';

export interface IFooterProps {
  /**
   * Adds class to the footer component
   */
  className?: string;
  children:
    | React.ReactElement<
        | typeof FooterBrand
        | typeof FooterLogo
        | typeof FooterPoliciesLinks
        | typeof FooterSocialMediaLinks
        | typeof FooterUsefulLinks
      >[]
    | React.ReactElement<
        | typeof FooterBrand
        | typeof FooterLogo
        | typeof FooterPoliciesLinks
        | typeof FooterSocialMediaLinks
        | typeof FooterUsefulLinks
      >;
  [others: string]: any;
}

const Footer: React.FC<IFooterProps> = (props) => {
  const {className, children, ...rest} = props;

  const [dimensions, setDimensions] = useState({width: window.innerWidth, height: window.innerHeight});

  const handleResize = () => {
    setDimensions({width: window.innerWidth, height: window.innerHeight});
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const renderFooter = () => {
    let contentFooterUsefulLinks: any = null;
    let contentFooterSocialMediaLinks: any = null;
    let contentFooterPoliciesLinks: any = null;
    let contentFooterLogo: any = null;
    let contentFooterBrand: any = null;

    if (children) {
      const _child = children;
      React.Children.forEach(_child, (_childItem: any) => {
        switch (_childItem.type) {
          case FooterUsefulLinks:
            contentFooterUsefulLinks = _childItem;
            break;
          case FooterSocialMediaLinks:
            contentFooterSocialMediaLinks = _childItem;
            break;
          case FooterPoliciesLinks:
            contentFooterPoliciesLinks = _childItem;
            break;
          case FooterBrand:
            contentFooterBrand = _childItem;
            break;
          case FooterLogo:
            contentFooterLogo = _childItem;
            break;
        }
      });
    }

    const header = contentFooterUsefulLinks || contentFooterSocialMediaLinks;
    const bottom = contentFooterLogo || contentFooterPoliciesLinks || contentFooterBrand;

    let bottomContainer = (
      <div className="footer-bottom">
        {contentFooterLogo}
        {contentFooterPoliciesLinks}
        {contentFooterBrand}
      </div>
    );

    if (dimensions.width <= 672)
      bottomContainer = (
        <div className="footer-bottom">
          {contentFooterPoliciesLinks}
          <div className="footer-bottom-items">
            {contentFooterLogo}
            {contentFooterBrand}
          </div>
        </div>
      );

    return (
      <div
        className={`footer-container ${className || ''}`}
        data-testid={rest && rest['data-testid'] ? `${rest['data-testid']}` : undefined}
        {...rest}
      >
        {header && (
          <div className="footer-header">
            {contentFooterUsefulLinks}
            {contentFooterSocialMediaLinks}
          </div>
        )}
        {header && bottom && <hr className="mt4 mb4" />}
        {bottom && bottomContainer}
      </div>
    );
  };

  return renderFooter();
};

export default Footer;
